.search-content {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 80%;
  margin: 0px 10%;
}

.search-header {
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 50px;
  font-size: 27px;
  -webkit-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.search-header.hidden {
  opacity: 0;
}

.search-header .movie-icon {
  margin-right: 15px;
  fill: white;
  font-size: 32px;
  -ms-flex-item-align: center;
      align-self: center;
  opacity: 0.9;
}

.search-header .total-results {
  -ms-flex-item-align: center;
      align-self: center;
}

.search-header .new-movie-button {
  margin-left: auto;
  cursor: pointer;
  opacity: 0.7;
  font-size: 22px;
  -ms-flex-item-align: center;
      align-self: center;
  -webkit-transition: opacity 0.7s;
  -o-transition: opacity 0.7s;
  transition: opacity 0.7s;
}

.search-header .new-movie-button:hover {
  opacity: 1;
  -webkit-transition: opacity 0.7s;
  -o-transition: opacity 0.7s;
  transition: opacity 0.7s;
}

.search-header .return-button {
  margin-left: 7px;
}

.search-header span {
  margin: 0px;
  font-weight: 100;
}


